
@font-face {
  font-family: OpenSans;
  src: url("/static/fonts/OpenSans-Regular.ttf");
}
@font-face {
  font-family: RobotoThin;
  src: url("https://fonts.googleapis.com/css?family=Roboto+Slab:100");
}
@font-face {
  font-family: Roboto;
  src: url("https://fonts.googleapis.com/css?family=Roboto+Slab");
}
header {
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #333333;
}
header ul {
    display: block;
    float: right;
}
header ul li {
      padding-right: 10px;
      float: left;
      text-transform: uppercase;
      font-weight: bold;
      color: #ffffff;
}
.campaign {
  background-color: beige;
}
.testimonial {
  background-color: red;
}
.campaign-ribbon {
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #333333;
  background-image: url("http://via.placeholder.com/2380x100");
  background-position: center;
}
.campaign-ribbon h4 {
    font-family: 'RobotoThin', serif;
    text-transform: uppercase;
    letter-spacing: 7px;
    font-weight: lighter;
    color: #ffffff;
    text-align: center;
    font-size: 40px;
}
.so-me {
  display: inline-block;
  width: 100%;
  padding-top: 65px;
  padding-bottom: 70px;
  background-color: #ffffff;
  -ms-flex-line-pack: center;
      align-content: center;
}
.so-me .message {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
}
.so-me .message .centered {
      display: block;
}
.so-me .message .centered svg {
        height: 30px;
        width: 100px;
}
.so-me .message .centered svg line {
          width: 100px;
          stroke-width: 1px;
          stroke: #333333;
}
.so-me .message .centered svg text {
          font-family: 'Roboto', serif;
          font-size: 18px;
          font-weight: bold;
          text-transform: uppercase;
          fill: #333333;
}
.so-me .so-me-icon {
    margin-top: 40px;
}
.so-me .so-me-icon ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.so-me .so-me-icon ul li {
        padding-right: 40px;
        float: left;
}
.vue-footer {
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 70px;
  padding-bottom: 70px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: justify;
      align-content: space-between;
  background-color: #333333;
  line-height: 40px;
}
.vue-footer div {
    min-width: 20%;
}
.vue-footer h5 {
    font-size: 18px;
    text-transform: uppercase;
    font-weight: semi-bold;
    color: #ffffff;
}
.vue-footer li {
    text-transform: capitalize;
    font-size: 16px;
    color: #7b7b7b;
    -webkit-transition: color 0.2s ease;
    transition: color 0.2s ease;
}
.vue-footer li:hover {
      color: #cfcfcf;
}
.vue-footer .pay-type ul {
    margin-top: 3.3vw;
    display: block;
}
.vue-footer .pay-type ul li {
      padding-right: 10px;
      float: left;
}
.vue-footer .identity {
    margin-top: 3vw;
}
.vue-footer .identity p {
      text-align: center;
      color: #7b7b7b;
}
.product {
  position: relative;
  display: inline-block;
  width: 270px;
}
.product img {
    margin-bottom: 14px;
}
.product .sale {
    z-index: 99;
    width: 70px;
    height: 35px;
    position: absolute;
    background-color: #33d0de;
    text-align: center;
    text-transform: uppercase;
    overflow: hidden;
    display: inline-block;
    color: #ffffff;
    line-height: 35px;
}
.product .inspect {
    background-color: rgba(255, 255, 255, 0.9);
    position: absolute;
    top: 275px;
    width: 100%;
    height: 125px;
    padding-top: 20px;
}
.product .inspect ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.product .inspect ul li {
        padding-right: 40px;
        float: left;
}
.product h3 {
    text-align: center;
    font-size: 16px;
    font-weight: normal;
    margin-bottom: 25px;
    color: #333333;
}
.product p {
    letter-spacing: 2px;
    color: #333333;
}
.product .add-to-bag {
    float: right;
    position: relative;
    top: -35px;
    border-width: 1.5px;
    border-style: solid;
    border-color: #333333;
    padding: 10px 20px 10px 20px;
    text-transform: uppercase;
    text-decoration: none;
    color: #333333;
}
.gallery-container {
  padding-left: 10vw;
  padding-right: 10vw;
}
.product-gallery {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: justify;
      align-content: space-between;
}
.product-gallery li {
    padding-right: 30px;
}
* {
  margin: 0;
  padding: 0;
  list-style-type: none;
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
  font-family: 'Open Sans', sans-serif;
}
